import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { User } from '../types';
import {
  hashPassword,
  verifyPassword,
  generateSecureId,
  checkRateLimit,
  recordLoginAttempt,
  createSession,
  isSessionValid,
  updateSessionActivity,
  Session,
  secureStorage
} from '../utils/security';

interface AuthContextType {
  user: User | null;
  login: (email: string, password: string) => Promise<{ success: boolean; message: string }>;
  register: (email: string, password: string, name: string, company?: string) => Promise<{ success: boolean; message: string }>;
  logout: () => void;
  updateProfile: (updates: Partial<Pick<User, 'name' | 'email' | 'company'>>) => void;
  isLoading: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

interface AuthProviderProps {
  children: ReactNode;
}

interface StoredUser {
  id: string;
  email: string;
  passwordHash: string;
  name: string;
  company?: string;
  createdAt: number;
  suspended?: boolean;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Check session on mount
  useEffect(() => {
    const checkSession = () => {
      const storedUser = secureStorage.getItem<User>('expedium_user');
      const session = secureStorage.getItem<Session>('expedium_session');

      if (storedUser && session && isSessionValid(session)) {
        // Check if user has been suspended by admin
        const allUsers = secureStorage.getItem<StoredUser[]>('expedium_users', []) || [];
        const currentUser = allUsers.find((u: StoredUser) => u.id === storedUser.id);
        if (currentUser?.suspended) {
          // User was suspended, log them out
          setUser(null);
          secureStorage.removeItem('expedium_user');
          secureStorage.removeItem('expedium_session');
          document.body.classList.remove('dark-mode');
          setIsLoading(false);
          return;
        }

        setUser(storedUser);
        // Update session activity
        secureStorage.setItem('expedium_session', updateSessionActivity(session));
        // Restore dark mode preference
        if (localStorage.getItem('expedium_darkMode') === 'true') {
          document.body.classList.add('dark-mode');
        }
      } else if (storedUser) {
        // Session expired, clear user
        secureStorage.removeItem('expedium_user');
        secureStorage.removeItem('expedium_session');
        document.body.classList.remove('dark-mode');
      }
      setIsLoading(false);
    };

    checkSession();

    // Periodically check session validity
    const intervalId = setInterval(checkSession, 60000); // Every minute

    return () => clearInterval(intervalId);
  }, []);

  const login = async (email: string, password: string): Promise<{ success: boolean; message: string }> => {
    // Normalize email
    const normalizedEmail = email.toLowerCase().trim();

    // Check rate limit
    const rateLimit = checkRateLimit(normalizedEmail);
    if (!rateLimit.allowed) {
      return { success: false, message: rateLimit.message };
    }

    const users = secureStorage.getItem<StoredUser[]>('expedium_users', []) || [];
    const foundUser = users.find((u: StoredUser) => u.email === normalizedEmail);

    if (!foundUser) {
      recordLoginAttempt(normalizedEmail, false);
      return { success: false, message: 'Invalid email or password' };
    }

    // Check if account is suspended
    if (foundUser.suspended) {
      return { success: false, message: 'Your account has been paused. Please contact support.' };
    }

    // Verify password - support both old (plain) and new (hashed) passwords
    let passwordValid = false;
    let needsMigration = false;

    if (foundUser.passwordHash && foundUser.passwordHash.length === 64 && /^[0-9a-f]+$/.test(foundUser.passwordHash)) {
      // Properly hashed password (64-char hex SHA-256)
      passwordValid = await verifyPassword(password, foundUser.passwordHash);
    } else if (foundUser.passwordHash) {
      // passwordHash field exists but isn't a valid hash  treat as plain text
      if (foundUser.passwordHash === password) {
        passwordValid = true;
        needsMigration = true;
      }
    }

    // Fallback: check old 'password' field for pre-security users
    if (!passwordValid && (foundUser as unknown as { password?: string }).password) {
      const oldPassword = (foundUser as unknown as { password: string }).password;
      if (oldPassword === password) {
        passwordValid = true;
        needsMigration = true;
      }
    }

    // Migrate plain-text password to hash
    if (passwordValid && needsMigration) {
      const newHash = await hashPassword(password);
      const updatedUsers = users.map((u: StoredUser) =>
        u.email === normalizedEmail
          ? { ...u, passwordHash: newHash, password: undefined }
          : u
      );
      secureStorage.setItem('expedium_users', updatedUsers);
    }

    if (passwordValid) {
      recordLoginAttempt(normalizedEmail, true);

      const userWithoutPassword: User = {
        id: foundUser.id,
        email: foundUser.email,
        name: foundUser.name,
        company: foundUser.company
      };

      setUser(userWithoutPassword);
      secureStorage.setItem('expedium_user', userWithoutPassword);
      secureStorage.setItem('expedium_session', createSession(foundUser.id));

      // Restore dark mode preference after login
      if (localStorage.getItem('expedium_darkMode') === 'true') {
        document.body.classList.add('dark-mode');
      }

      return { success: true, message: 'Login successful' };
    }

    recordLoginAttempt(normalizedEmail, false);
    const remainingAttempts = checkRateLimit(normalizedEmail).remainingAttempts;
    return {
      success: false,
      message: remainingAttempts !== undefined && remainingAttempts <= 2
        ? `Invalid email or password. ${remainingAttempts} attempts remaining.`
        : 'Invalid email or password'
    };
  };

  const register = async (
    email: string,
    password: string,
    name: string,
    company?: string
  ): Promise<{ success: boolean; message: string }> => {
    // Normalize email
    const normalizedEmail = email.toLowerCase().trim();

    const users = secureStorage.getItem<StoredUser[]>('expedium_users', []) || [];
    const existingUser = users.find((u: StoredUser) => u.email === normalizedEmail);

    if (existingUser) {
      return { success: false, message: 'An account with this email already exists' };
    }

    // Hash password before storing
    const passwordHash = await hashPassword(password);

    const newUser: StoredUser = {
      id: generateSecureId(),
      email: normalizedEmail,
      passwordHash,
      name: name.trim(),
      company: company?.trim(),
      createdAt: Date.now()
    };

    users.push(newUser);
    secureStorage.setItem('expedium_users', users);

    const userWithoutPassword: User = {
      id: newUser.id,
      email: newUser.email,
      name: newUser.name,
      company: newUser.company
    };

    setUser(userWithoutPassword);
    secureStorage.setItem('expedium_user', userWithoutPassword);
    secureStorage.setItem('expedium_session', createSession(newUser.id));

    return { success: true, message: 'Registration successful' };
  };

  const logout = () => {
    setUser(null);
    secureStorage.removeItem('expedium_user');
    secureStorage.removeItem('expedium_session');
    // Clean up dark mode so login page renders correctly
    document.body.classList.remove('dark-mode');
  };

  const updateProfile = (updates: Partial<Pick<User, 'name' | 'email' | 'company'>>) => {
    if (!user) return;

    // Create updated user object
    const updatedUser: User = {
      ...user,
      ...updates
    };

    // Update state
    setUser(updatedUser);

    // Update expedium_user in storage
    secureStorage.setItem('expedium_user', updatedUser);

    // Update user in expedium_users array
    const users = secureStorage.getItem<StoredUser[]>('expedium_users', []) || [];
    const updatedUsers = users.map((u: StoredUser) =>
      u.id === user.id
        ? { ...u, name: updatedUser.name, email: updatedUser.email, company: updatedUser.company }
        : u
    );
    secureStorage.setItem('expedium_users', updatedUsers);
  };

  return (
    <AuthContext.Provider value={{ user, login, register, logout, updateProfile, isLoading }}>
      {children}
    </AuthContext.Provider>
  );
};
